let canvas = document.getElementById('canvas');
let context = canvas.getContext('2d');

const bird = new Image();
const bg = new Image();
const foreGround = new Image();
const pipeUp = new Image();
const pipeBottom = new Image();

// –°–æ–∑–¥–∞–µ–º –≤—Å–µ –æ–±—å–µ–∫—Ç—ã –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏
bird.src = 'img/flappy_bird_bird.png';
bg.src = 'img/flappy_bird_bg.png';
foreGround.src = 'img/flappy_bird_fg.png';
pipeUp.src = 'img/flappy_bird_pipeUp.png';
pipeBottom.src = 'img/flappy_bird_pipeBottom.png';

// –ê—É–¥–∏–æ
let fly = new Audio();
let score_audio = new Audio();

fly.src = 'audio/fly.mp3';
score_audio.src = 'audio/score.mp3';


// –æ—Ç—Å—Ç—É–ø –º–µ–∂–¥—É –ø—Ä–µ–ø—è—Ç—Å—Ç–≤–∏—è–º–∏
let gap = 90;


// —Å–ª—É—à–∞–µ–º –Ω–∞–∂–∞—Ç–∏–µ –Ω–∞ –ª—é–±—É—é –∫–ª–∞–≤–∏—à—É
document.addEventListener('keydown', moveUp);

function moveUp() {
    yPosition -= 20;
    fly.play();
}

// –±–ª–æ–∫–∏ –ø—Ä–µ–ø—è—Ç—Å—Ç–≤–∏–π
let pipe = [];

pipe[0] = {
    x: canvas.width,
    y: 0
}

let score = 0;
// –ø–æ–∑–∏—Ü–∏—è –ø—Ç–∏—á–∫–∏
let xPosition = 10;
let yPosition = 150;
let gravity = 1;

// —Ä–∏—Å—É–µ–º –≤—Å–µ –µ–ª–µ–º–µ–Ω—Ç—ã –≤ –∫–∞–Ω–≤–∞—Å–µ
function draw() {
    context.drawImage(bg, 0, 0);

    for(let i = 0; i < pipe.length; i++) {
        context.drawImage(pipeUp, pipe[i].x, pipe[i].y);
        context.drawImage(pipeBottom, pipe[i].x, pipe[i].y+ pipeUp.height + gap);

        pipe[i].x--;

        // –¥–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤—ã–µ –ø—Ä–µ–ø—è—Ç—Å—Ç–≤–∏—è
        if (pipe[i].x === 125) {
            pipe.push({
                x: canvas.width,
                y: Math.floor(Math.random() * pipeUp.height) - pipeUp.height
            })
        }

        // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∏–ª–∏ –ø—Ç–∏—á–∫–∞ –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –≤ –ø—Ä–µ–¥–µ–ª–∞—Ö —à–∏—Ä–∏–Ω—ã –ø—Ä–µ–ø—è—Ç—Å—Ç–≤–∏–µ –∏ –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –ª–∏ –æ–Ω–∞ –≤ –ø—Ä–µ–¥–µ–ª–∞—Ö –µ–≥–æ –≤—ã—Å–æ—Ç—ã
        if( xPosition + bird.width >= pipe[i].x
            && xPosition <= pipe[i].x + pipeUp.width
            && (yPosition <= pipe[i].y + pipeUp.height
            || yPosition + bird.height >= pipe[i].y + pipeUp.height + gap) || yPosition + bird.height >= canvas.height - foreGround.height) {
                location.reload(); // reload the page
            }
        
            // –µ—Å–ª–∏ –ø—Ä–µ–ø—è—Ç—Å—Ç–≤–∏–µ –ø—Ä–æ–π–¥–µ–Ω–æ –∏ —É–∂–µ –≤–Ω–∞—á–∞–ª–µ –Ω–∞—à–µ–≥–æ —Ñ—Ä–µ–π–º–∞ —É–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Å—á–µ—Ç
            if (pipe[i].x == 5) {
                score++;
                score_audio.play();
            } 
    }

    context.drawImage(foreGround, 0, canvas.height - foreGround.height);
    context.drawImage(bird, xPosition, yPosition);

    yPosition += gravity;

    context.fillStyle = "#000";
    context.font = "24px Verdana";
    context.fillText("–°—á–µ—Ç: " + score, 10, canvas.height - 20);

    requestAnimationFrame(draw);
}

pipeBottom.onload = draw;